import { graphql, type HeadFC, type PageProps } from 'gatsby';
import { getImage } from 'gatsby-plugin-image';
import * as React from 'react';
import { PageLayout } from '../components/PageLayout';
import { CustomHead } from '../components/CustomHead';

const IndexPage: React.FC<PageProps<Queries.IndexPageQuery>> = ({ data }) => {
  const image = data.headerImage
    ? getImage(data.headerImage.childImageSharp)
    : null;

  return (
    <PageLayout image={image} title="Solution engineering by BCM">
      <div className="container mx-auto px-4 lg:px-0">
        <h1 className="my-8 text-4xl font-bold lg:text-5xl">
          Tailored Enterprise Software Solutions for Your Business
        </h1>
        <p>
          At BlackCatMatters, we specialize in designing and developing
          cutting-edge enterprise software solutions that meet your business
          needs. Whether you're looking to streamline operations, enhance
          productivity, or create a bespoke application, our team is here to
          help. Feel free to reach out to us to discuss your project
          requirements, goals, and how we can support your success. We're
          available for consultations and are committed to providing a prompt
          response to all inquiries. You can connect with us directly here or
          via Malt. We're based in France but offer our services worldwide,
          working remotely to ensure seamless collaboration regardless of your
          location or time zone. Letâ€™s create something exceptional together.
        </p>
        <p className="mt-6">
          <a
            href="mailto:contact@blackcatmatters.com"
            className="px-1 py-1 font-bold text-black"
          >
            Contact Us.
          </a>
          We also are on
          <a
            href="https://www.malt.fr/profile/audefalco/"
            className="px-1 py-1 font-bold text-black"
          >
            Malt.
          </a>
        </p>
      </div>
    </PageLayout>
  );
};

export default IndexPage;

// This query will be run during the GatsbyJS build process when creating our
// pages. The data will be passed to our page component as a `data` property.
export const pageQuery = graphql`
  query IndexPage {
    headerImage: file(relativePath: { eq: "header.jpg" }) {
      childImageSharp {
        gatsbyImageData(layout: FULL_WIDTH)
      }
    }
  }
`;

export const Head: HeadFC = () => (
  <>
    <title>Home Page</title>
    <CustomHead
      title="Home | My Gatsby Blog"
      description="This is the home page to my blog. You should write a better description."
    />
  </>
);
